/*imports*/
@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import url('https://fonts.googleapis.com/css?family=Lobster');

/* variables */
:root {
    --toastify-color-light: rgb(14, 13, 13);
  /* colors */
  --primary-color: #f44336;
  --primary-color-dark: color(#f44336 shade(12%));
  --primary-color-tint: color(#f44336 tint(70%));
  --secondary-color: rgb(255, 251, 251);
  --secodnary-color-dark: color(#222 shade(12%));
  --secondary-color-tint: color(#222 tint(70%));
  
  /* width */
  --form-width: 16rem;
}
input:-webkit-autofill,

input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: rgb(218, 211, 211);
    
}


body {
  color: #fff;
  background: #222;
  font-family: 'Open Sans', sans-serif;
}

.form {
  width: 16rem;
  width: var(--form-width);
  display: flex;
  padding-top: 70px;
  flex-direction: column;
  align-items: center;
  margin: 1rem auto 0 auto;
  
}

.form_logo {
    font-family: 'Lobster', cursive;
    font-size: 2.5rem;
    margin-bottom: 8px;
  }

.form_logo span {
      color: #f44336;
      color: var(--primary-color);
    }

.form_title{
    font-weight: bold;
    margin-bottom: 8px;
  }

.form_title span {
      color: #f44336;
      color: var(--primary-color);
    }

.form_items{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

.form_inputs{
    width: 16rem;
    width: var(--form-width);
    color:rgb(218, 211, 211);
    margin: 1rem 0;
    position: relative;
  }
.loader{
    color: red;
}
.form_inputs label {
      font-size: 16px;
      position: absolute;
      top: 10px;
      left: 0;
      pointer-events: none;
      transition: all 0.15s ease;
    }

.form_inputs input {
      width: 100%;
      height:36px;
      background: inherit;
      border: none;
      border-bottom: 1px solid;
      color: inherit;
    }

.form_inputs input:focus, .form_inputs input:valid{
        outline: none;
        border-width: 2px;
        margin-bottom: -1px;
        border-color: #f44336;
        border-color: var(--primary-color);
      }

.form_inputs input:focus + label, .form_inputs input:valid + label{
        transform: translateY(-20px);
        font-size: 70%;
        color: #f44336;
        color: var(--primary-color);
      }

.form_button {
    width: 100%;
    height: 36px;
    margin-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
    border: none;
    border-radius: 2px;
    color: white;
    background: #f44336;
    background: var(--primary-color);
    box-shadow:2px 2px 2px color(#f44336 shade(12%));
    box-shadow:2px 2px 2px var(--primary-color-dark);
  }
  .form_button1 {
    width: 100%;
    height: 36px;
    margin-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
    border: none;
    border-radius: 2px;
    color: white;
    background: #f1b2ad;
    box-shadow:2px 2px 2px color(#f44336 shade(12%));
    box-shadow:2px 2px 2px var(--primary-color-dark);
  }
.alert{
    color: red;
    font-size: 12px;
}
.form_button:focus {
      outline: none;
    }

.form_button:active {
      outline: none;
      background: color(#f44336 shade(12%));
      background: var(--primary-color-dark);
    }

.form_others {
 margin-top: 20px;
 

  }

.authLink {
      color: #0289b3;
      text-align: center;
     
      text-decoration: none;
    }